import React, { Component } from 'react';
import axios from '../../../axios';

import './FullPost.css';

class FullPost extends Component {
    state = {
        loadedPost: null
    }

    componentDidMount() {
        if (this.props.match.params.codename) {
            if (!this.state.loadedPost || (this.state.loadedPost && this.state.loadedPost.item.system.codename !== this.props.codename)) {
                axios.get('/items/' + this.props.match.params.codename)
                    .then(response => {
                        this.setState({ loadedPost: response.data });
                    });
            }
        }
    }

    triggerEditPost() {
        console.log("aaaaa")
    }

    render() {
        let post = "";
        if (this.props.codename) {
            post = <p style={{ textAlign: 'center' }}>Loading...!</p>;
        }

        if (this.state.loadedPost) {
            post = (

                <div className="blog-post">
                    <h1 className="blog-post__title">{this.state.loadedPost.item.elements.headline.value}</h1>
                    <div className="blog-post__author">by {this.state.loadedPost.item.elements.author.value}</div>
                    <div className="blog-post__info">
                        <div className="row">
                            <div className="col-sm-auto">
                                <small>
                                    <i className="fa fa-tags" aria-hidden="true"></i>
                                    CSS, JavaScript, Kentico
                                </small>
                            </div>
                        </div>
                    </div>
                    <div dangerouslySetInnerHTML={{ __html: this.state.loadedPost.item.elements.body_text.value }} />

                    <button className="btn btn-primary" onClick={this.triggerEditPost}>Edit post</button>

                    <br /><br /><br />

                    {/* <form className="hide">
                        <h2>Edit Post</h2>
                        <div className="form-group">
                            <label>Title</label>
                            <input className="form-control" type="text" placeholder={this.state.loadedPost.item.elements.headline.value} />
                        </div>

                        <div className="form-group">
                            <label>Author</label>
                            <input className="form-control" type="text" placeholder={this.state.loadedPost.item.elements.author.value} />
                        </div>

                        <div className="form-group">
                            <label>Content</label>
                            <textarea className="form-control" rows="4" placeholder="New content" />
                        </div>

                        <button className="btn btn-primary">Save Post</button>
                    </form> */}
                </div>
            );
        }
        return post;
    }
}

export default FullPost;


