import React, { Component } from 'react';
import { Route, NavLink } from 'react-router-dom';
import './Blog.css';
import FullPost from './FullPost/FullPost';
import Posts from './Posts/Posts';
import NewPost from './NewPost/NewPost';

class Blog extends Component {
    render() {


        return (
            <div className="Blog">
                <nav className="navbar navbar-expand-lg navbar-light" style={{backgroundColor: "#e3f2fd"}}>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item"><NavLink exact className="nav-link" to="/kentico-cloud">Home</NavLink></li>
                            <li className="nav-item"><NavLink className="nav-link" to="/new-post">New article</NavLink></li>
                        </ul>
                    </div>
                </nav>

                <Route path="/kentico-cloud" exact component={Posts} />
                <Route path="/new-post" component={NewPost} />
                <Route path="/posts/author/:authorName" exact component={Posts} />
                <Route path="/posts/:codename" exact component={FullPost} />
            </div>
        );
    }
}

export default Blog;


