import React, { Component } from 'react';
import axios from 'axios';
import { Redirect } from 'react-router-dom';

import './NewPost.css';

class NewPost extends Component {
    state = {
        title: '',
        content: '',
        author: '',
        submitted: false
    }

    componentDidMount() {
        // If unauth => this.props.history.replace('/posts');
    }

    postDataHandler = () => {
        const data = {
            title: this.state.title,
            body: this.state.content,
            author: this.state.author
        };
        axios.post('/posts', data)
            .then(response => {
                console.log(response);
                this.props.history.replace('/posts');
                // this.setState( { submitted: true } );
            });
    }

    render() {
        let redirect = null;
        if (this.state.submitted) {
            redirect = <Redirect to="/posts" />;
        }
        return (
            <form>
                {redirect}
                Work in progress!!
                <h2>Add a Post</h2>
                <div className="form-group">
                    <label>Title</label>
                    <input className="form-control" type="text" value={this.state.title} onChange={(event) => this.setState({ title: event.target.value })} />
                </div>

                <div className="form-group">
                    <label>Content</label>
                    <textarea className="form-control" rows="4" value={this.state.content} onChange={(event) => this.setState({ content: event.target.value })} />
                </div>

                <div className="form-group">
                    <label>Author</label>
                    <select className="form-control" value={this.state.author} onChange={(event) => this.setState({ author: event.target.value })}>
                        <option value="Max">Alina</option>
                        <option value="Manu">Ralu</option>
                    </select>
                </div>
                <button className="btn btn-primary" onClick={this.postDataHandler}>Add Post</button>
            </form>
        );
    }
}

export default NewPost;


